<template>
  <div class="main-bottom">
    <div class="main-bottom-left">
      <div class="image">
        <img src="@/assets/images/star.png" />
      </div>
      <div class="imgDesc">
        联系
        <span>专家团队</span>
        <br />为您制定解决方案
      </div>
    </div>
    <div class="main-bottom-right">
      <div class="main-bottom-form">
        <div class="input-wrapper">
          <input
            v-model="formData.phoneNumber"
            placeholder="请输入您的电话或邮箱 (必填)"
            type="text"
          />
        </div>
        <div class="textarea-wrapper">
          <textarea
            v-model="formData.problem"
            :rows="5"
            maxlength="200"
            placeholder="请输入您想了解的内容(选填)"
          />
          <div class="percent">{{ formData.problem?.length || 0 }} / 200</div>
        </div>
        <div class="submit-btn">
          <b-button @click="submitForm">提 交</b-button>
        </div>
      </div>
    </div>
    <div class="zd_code">
      <img src="@/assets/images/code2.png" />
    </div>
  </div>
</template>

<script>
import { insertSolution, insertClickrecord } from "@/api/request.js";
export default {
  name: "contact-wrapper",
  props: {},
  components: {},
  data() {
    return {
      formData: {},
    };
  },
  created() {},
  mounted() {},
  watch: {},
  computed: {},
  methods: {
    showToast({ title, content, type }) {
      this.$bvToast.toast(content || "这是一条 Toast 消息", {
        title: title || "提示消息",
        variant: type || "success",
        toaster: "b-toaster-top-center",
        autoHideDelay: 2000,
      });
    },
    //提交
    submitForm() {
      let testStr = this.formData.phoneNumber;
      if (!testStr) {
        return this.showToast({
          content: "电话/邮箱不能为空",
          type: "danger",
        });
      }
      var regPhone = /^[1][3-9][0-9]{9}$/;
      var regEMail = /^([a-zA-Z0-9]+[-_.]?)+@[a-zA-Z0-9]+\.[a-z]+$/;
      if (!regPhone.test(testStr) && !regEMail.test(testStr) && testStr != "") {
        return this.showToast({
          content: "请输入正确的电话/邮箱",
          type: "danger",
        });
      } else {
        insertSolution(this.formData)
          .then((res) => {
            if (res.data.code == 200) {
              this.showToast({
                content: "已提交，我们会及时联系您",
              });
              this.buttonClick("提交");
            } else {
              this.showToast({
                content: res.message,
                type: "danger",
              });
            }
          })
          .catch(() => {});
      }
    },
    //按钮点击记录
    buttonClick(val) {
      let params = {
        clickedItem: val,
        clickedCookie: this.$cookies.get("token"),
      };
      insertClickrecord(params)
        .then(() => {})
        .catch(() => {});
    },
  },
};
</script>

<style lang="less" scoped>
// 专家
.main-bottom {
  width: 70%;
  margin: 40px auto;
  display: flex;
  justify-content: space-between;
  .main-bottom-left {
    height: 100%;
    text-align: right;
    .image {
      width: 100%;
      height: 130px;
      display: flex;
      justify-content: right;
      img {
        display: block;
        width: 100px;
        height: 100px;
      }
    }
    .imgDesc {
      font-size: 36px;
      font-family: PingFangSC-Semibold;
      font-weight: 600;
      text-align: right;
      span {
        color: #f7b024;
      }
    }
  }
  .main-bottom-right {
    flex: 1;
    height: 100%;
    font-size: 14px;
    margin: 0 5%;
    .main-bottom-form {
      width: 100%;
      height: 70%;
      // padding-left: 80px;
      input {
        background-color: #f0f0f0;
        border-radius: 20px;
        border: 1px solid #dcdfe6;
        outline: none;
        padding: 6px 15px;
        width: 100%;
      }
      .textarea-wrapper {
        margin: 30px 0 20px;
        textarea {
          background-color: #f0f0f0;
          border-radius: 20px;
          border: 1px solid #dcdfe6;
          outline: none;
          padding: 6px 15px;
          width: 100%;
        }
        .percent {
          text-align: right;
        }
      }
      .submit-btn {
        text-align: left;
        button {
          width: 120px;
        }
      }
    }
  }
  .zd_code {
    // width: 25%;
    // height: 81%;
    // display: flex;
    // justify-content: center;
    // align-items: center;
    img {
      // width: 150px;
      // height: 150px;
      // display: block;
    }
  }
}
</style>